<template>
  <div class="straightBasket">
    <v-container fluid>
      <v-layout
        row
        wrap
        class="p-0 my-0">
        <v-flex
          md9
          xs12
          tag="h3"
          class="my-0 pb-3">
          {{ $ln(gname) }} - {{ $ln('過關') }}
        </v-flex>

        <!-- 每秒更新 -->
        <CommonCounter
          :reciprocalItems.sync="reciprocalItems"
          :updateTableList="updateTableList"
        />
      </v-layout>

      <v-layout
        row
        wrap>
        <SportBallStatus
          :dataSelect.sync="selectDateItems.selected"
          :items="selectDateItems.options"
          :setLanguage="false"
          title="日期"
          class="selectHide"
        />

        <SportBallStatus
          :dataSelect.sync="selectChangeItems.selected"
          :items="selectChangeItems.options"
          title="拆帳"
        />

        <v-flex
          md3
          xs6
          class="px-1">
          <v-layout
            row
            wrap>
            <SportBallStatus
              classType="classType"
              :dataSelect.sync="selectRowsPerPageItems.selected"
              :items="selectRowsPerPageItems.options"
              title="每頁顯示"
            />
          </v-layout>
        </v-flex>
        <v-flex
          xs4
          md2
          class="grey--text text--darken-2 d-flex align-center">
          {{ $ln('共 %s 筆', pagination.totalItems) }}
        </v-flex>
      </v-layout>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else class="pt-1">
        <SportPassTable
          :header.sync="headers"
          :contents.sync="sportsList"
          :path="path"
          gameType="football"
        />

        <div class="text-xs-center pt-2">
          <v-pagination
            v-model="pagination.currentPage"
            :length="pages"
            color="deep-orange darken-1"
            @input="loadNextPage"/>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import Pass from '@/mixins/sport/Pass'

export default {
  name: 'FootballPass',
  mixins:[Pass],
  data () {
    return {
      gname: '美足',
      // 連結注單明細
      path: '/manage/sport/football/AmountFoot',
      headers: {
        // 'league_name': { text: '聯盟名稱', align: 'center', sortable: false }, // 已被獨立成一行
        'count_date': { text: '帳務日期', align: 'center', sortable: false },
        'game_time': { text: '比賽時間', align: 'center', sortable: false },
        'team': { text: '主客隊', align: 'center', sortable: false },
        'A': { text: '讓分', align: 'center', sortable: false },
        // { text: '金額', align: 'center', sortable: false }, // 金額併入讓分欄位
        'BS': { text: '大小', align: 'center', sortable: false },
        // { text: '金額', align: 'center', sortable: false }, // 金額併入讓分欄位
        'SD': { text: '單雙', align: 'center', sortable: false },
        // { text: '金額', align: 'center', sortable: false }, // 金額併入讓分欄位
        'OL': { text: '獨贏', align: 'center', sortable: false }
        // { text: '金額', align: 'center', sortable: false } // 金額併入讓分欄位
      },
      // 單式球類傳入參數
      gamelist: {
        act: 'allup',
        ball: 8,
        game_type: 0,
        time_interval: '',
        start: '',
        occupy: '',
        count_date: '',
        page: 1,
        count: ''
      }
    }
  }
}
</script>
