
<script>
import { mapGetters, mapActions } from 'vuex'
import $ from 'jquery'
import numeral from 'numeral'

export default {
  name: 'SportPass',
  filters: {
    // 金錢數字格式
    amountFormat (value) {
      return numeral(value).format('0,0')
      Math.round(number)
    }
  },
  data () {
    return {
      // 日期選擇
      selectDateItems: {
        selected: 1,
        options: []
      },
      // 秒數倒數更新
      reciprocalItems: {
        selected: 60,
        options: [
          { text: '不更新', value: '' },
          { text: '每5秒更新', value: 5 },
          { text: '每10秒更新', value: 10 },
          { text: '每20秒更新', value: 20 },
          { text: '每30秒更新', value: 30 },
          { text: '每60秒更新', value: 60 }
        ],
        // 倒數 timer
        updateListTimer: null,
        // 當前秒數
        currentSpeed: '',
        // 確定更新秒數
        confirmSpeed: ''
      },
      // 拆帳選擇
      selectChangeItems: {
        selected: 1,
        options: [
          { text: '100％', value: 1 },
          { text: '自己', value: 0 }
        ]
      },

      // 每頁顯示筆數
      selectRowsPerPageItems: {
        selected: 15,
        options: [
          { text: '15筆', value: 15 },
          { text: '30筆', value: 30 },
          { text: '60筆', value: 60 }
        ]
      },
      // 表格無資料顯示
      showNoData: '目前無資料',
      // 列表資料
      sportsList: {},
      pagination: {
        currentPage: 1, // 當前頁數
        rowsPerPage: 15, // 每頁筆數
        totalItems: 0 // 總筆數
      },
      newDate: new Date(),
      // 頁面載入狀態
      loadingStatus: false,
      // 單式球類傳入參數
      gamelist: {
        act: 'allup',
        ball: '',
        game_type: 0,
        time_interval: '',
        start: 1,
        occupy: 1,
        count_date: '',
        page: 1,
        count: ''
      }
    }
  },
  computed: {
    ...mapGetters({
      getSportsState: 'sports/getSportsState'
      // 載入個人帳號
      // getProfileState: 'account-management/getProfileState'
    }),
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  watch: {
    '$store.state.lang' (value) {
      // 刷新日期選單語系
      this.createSelectDateMenu(this.selectDateItems.selected)
    },
    getSportsState (payload) {
      // 調整獨立聯盟名稱位置而重組資料
      let list = {};
      // console.log('過關 data_list: ', payload.result.data_list);
      (payload.result.data_list).forEach(el => {
        let l_name = `${el.league_name}${el.transType}`
        if(!list[l_name]) list[l_name] = [] ;
        list[l_name].push(el)
      });
      this.sportsList = list
      // console.log('過關 sportsList: ', list);

      // 若 API 有當前頁數回傳資料，可設定如下
      this.pagination.currentPage = payload.result.current_page
      this.pagination.totalItems = payload.result.total_count
      // this.sportsList.contents = payload.result.list // ?不清楚用處?
    },
    'pagination.rowsPerPage' (rowsPerPage) {
      // 回第一頁
      this.pagination.currentPage = 1
      // console.log(1)
      // this.loadSports()
    },
    // (每頁顯示)切換
    'selectRowsPerPageItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      this.pagination.rowsPerPage = value
      // console.log(2)
      this.loadSports()
    },
    'selectItems.selected' (value) {
      this.pagination.rowsPerPage = value
      // console.log(3)
      this.loadSports()
    },
    // 每秒更新切換
    'reciprocalItems.selected' (value) {
      this.updateTableList()
    },
    'reciprocalItems.currentSpeed' (value) {
      // 倒數為0秒時刷新列表
      if (value === 0) {
        // console.log(4)
        this.loadSports()
      }
    },
    // (拆帳)切換
    'selectChangeItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      this.gamelist.occupy = value
      // console.log(5)
      this.loadSports()
    },
    'selectPageItems.selected' (value) {
      this.pagination.rowsPerPage = value
      // console.log(6)
      this.loadSports()
    },
    // (日期)切換
    'selectDateItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

    // console.log(this.newDate)
      var date = this.newDate.getFullYear() + '-' + (this.newDate.getMonth() + 1 < 10 ? '0' : '') + (this.newDate.getMonth() + 1) + '-' + (this.newDate.getDate() < 10 ? '0' : '') + this.newDate.getDate()
      // console.log(value)
      // console.log(date)
      // this.gamelist.start = value === date ? '' : 1
      this.gamelist.count_date = value
      // console.log(7)
      this.loadSports()
    },
    getProfileState (payload) {
      this.gamelist.account = payload.result.l_no
    }
  },
  mounted () {
    this.createSelectDateMenu()

    this.updateTableList(true)
  },
  destroyed () {
    // 清除 timer
    clearInterval(this.reciprocalItems.updateListTimer)
  },
  methods: {
    ...mapActions({
      getSports: 'api/getSports'
    }),
    // 載入過關
    loadSports (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        act: this.gamelist.act,
        ball: this.gamelist.ball,
        game_type: this.gamelist.game_type,
        time_interval: this.gamelist.time_interval,
        start: this.gamelist.start,
        occupy: this.gamelist.occupy ? this.gamelist.occupy : this.selectChangeItems.selected,
        count_date: this.selectDateItems.selected,
        // count_date: this.gamelist.count_date,
        page: page !== undefined ? page : this.pagination.currentPage,
        count: this.pagination.rowsPerPage = this.gamelist.count ? this.gamelist.count : this.selectRowsPerPageItems.selected
      }
      this.getSports(params)
        .then(res => {
          if (res.status === 200) {
            // 載入狀態(結束)
            this.loadingStatus = false
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 載入下一頁
    loadNextPage (page) {
      // 若 API 有當前頁數回傳資料，可註解以下
      this.pagination.currentPage = page
      // console.log(8)
      this.loadSports()
    },
    /**
     * 建立日期選單
     * @params  [currentSelected]  當前選擇日期
     **/
    createSelectDateMenu (currentSelected) {
      let days = [
        this.$ln('星期日'),
        this.$ln('星期一'),
        this.$ln('星期二'),
        this.$ln('星期三'),
        this.$ln('星期四'),
        this.$ln('星期五'),
        this.$ln('星期六')
      ]
      let options = []
      let newDate = new Date()
      newDate.setDate(newDate.getDate() + 1)
      for (let i = 0; i < 8; i++) {
        options.push({
          text: this.$ln(`%s月%s日 %s`, (newDate.getMonth() + 1), newDate.getDate(), days[newDate.getDay()]),
          value: `${newDate.getFullYear()}-${(newDate.getMonth() + 1 < 10 ? '0' : '')}${(newDate.getMonth() + 1)}-${(newDate.getDate() < 10 ? '0' : '')}${newDate.getDate()}`
        })
        newDate.setDate(newDate.getDate() - 1)
      }
      this.selectDateItems.selected = currentSelected ? currentSelected : options[1].value
      this.selectDateItems.options = options
    },
    /**
     * 秒數倒數
     * @params  [speed]  倒數秒數
     **/
    startCountdown (speed) {
      this.reciprocalItems.currentSpeed = speed
      this.reciprocalItems.updateListTimer = setInterval(() => {
        this.reciprocalItems.currentSpeed--
        // 秒數為負數，則重新倒數
        if (this.reciprocalItems.currentSpeed < 0) {
          // 清除 timer
          clearInterval(this.reciprocalItems.updateListTimer)
          // 重新倒數秒數
          this.startCountdown(this.reciprocalItems.confirmSpeed)
        }
      }, 1000)
    },
    // 啟動秒數倒數刷新表格資料
    updateTableList (first=false) {
      console.log(first)
      if(first===false) {
        console.log(9)
        this.loadSports()
      }

      // 確定更新秒數
      this.reciprocalItems.confirmSpeed = this.reciprocalItems.selected

      // 清除 timer
      clearInterval(this.reciprocalItems.updateListTimer)

      // 秒數為負數，則不更新倒數功能
      if (this.reciprocalItems.selected < 0) {
        // 當前秒數設置空值
        this.reciprocalItems.currentSpeed = ''
        return
      }
      // 倒數秒數
      this.startCountdown(this.reciprocalItems.confirmSpeed)
    }
  }
}
</script>
